import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Introduction () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>rules-fight-introduction</SubjectIdentifier>
      <SubjectKeyword>Règle</SubjectKeyword>
      <SubjectKeyword>Règle de combat</SubjectKeyword>
      <SubjectKeyword>Introduction</SubjectKeyword>
      <SubjectTitle>Introduction</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          Les scènes à interaction forte nécessitant des prises de décision
          simultanées sont résolues au tour par tour. Tout élément du récit
          pouvant agir de son propre chef lors d'une telle situation est
          appelé une <strong>entité</strong>.
        </p>

        <p>
          <em>Action.</em> Une <strong>action</strong> est une prise de décision
          unilatérale d'une entitée à un instant donné de la scène d'action. Une
          fois la décision ou les décisions d'une entitée résolue, une autre
          peut alors à son tour réaliser une action et ainsi de suite.
        </p>

        <p>
          <em>Round.</em> Un <strong>round</strong> est la durée nécéssaire
          pour que toutes les entitées d'une scène puissent réaliser l'ensemble
          des actions auxquelles elles ont droit.
        </p>

        <p>
          L'ensemble des actions nécessaires pour que toutes des entités d'une
          scène puissent agir est appelé un . Il peut
          arriver qu'une entité ait le droit de jouer plusieurs actions lors
          d'un round. La quantité d'actions jouée pendant un round et leur
          ordonnancement dépend des scores d'initiative de chaque entité et d'un
          accord tacite entre-elles.
        </p>
      </SubjectContent>
    </Subject>
  )
}
